import React from "react"
import emailjs from "emailjs-com"

function ContactUs(){
    function sendEmail(e) {
        e.preventDefault();
    
        emailjs.sendForm('service_gucevlj', 'template_4j1h22f', e.target, 'user_ZQ2Docibxup4sd5t5YzSe')
          .then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });
          e.target.reset()
      }

    return (
        <div id="contact">
            <h1 className="text-center p-4">Contact Us</h1>
            <div className="row m-0 p-4">
                <span className="col-6 text-center">
                    <b>
                        <i className="fa fa-phone mr-2"></i>0733.859.365
                    </b>
                </span>
                <span className="p-0"></span>
                <span className="col-5 ml-4">
                    <h5 className="text-center">Send A Message</h5>
                    <form className="contact-form" onSubmit={sendEmail}>
                        <label>Name</label>
                        <input type="text" name="name" className="form-control" />
                        <label>Subject</label>
                        <input type="text" name="subject" className="form-control"/>
                        <label>Email</label>
                        <input type="email" name="email" className="form-control"/>
                        <label>Message</label>
                        <textarea name="message" className="form-control"/>
                        <input className="btn btn-primary btn-sm mt-2" type="submit" value="Send" />
                    </form>
                </span>
            </div>
        </div>
      )
}

export default ContactUs