import React from "react"

function Item(props){
    return (
        <div className="col-4 mb-2 p-0 text-center item">
            <img src={`${process.env.PUBLIC_URL}/images/${props.item.image}`} alt="Clothes Item"/>
            <p>
                <h4>{props.item.name}</h4>
                <label>R{props.item.price}</label>
            </p>
        </div>
    )
}

export default Item