import React from 'react'

function Navigation(){
  return(
    <nav>
        <span>
            <img src={`${process.env.PUBLIC_URL}/images/logo/logos.png`} alt="Logo" onClick={() => {
                window.location.href = "https://abraham-silas.github.io/example/"
            }}/>
        </span>
        <span>
            <a href="https://abraham-silas.github.io/example/">Home</a>
            <a href="#collection">Collection</a>
            <a href="#contact">Contact</a>
        </span>
        <span>
            <i onClick={() => {
                window.location.href = "https://www.facebook.com/"
            }} className="fab fa-facebook"/>
            <i onClick={() => {
                window.location.href = "https://www.instagram.com/"
            }} className="fab fa-instagram"/>
        </span>
    </nav>)
 }

export default Navigation