import React, { Component } from "react"

class Preview extends Component{
    render(){
        return (
            <div id="preview" className="carousel slide" data-ride="carousel">
                <ol className="carousel-indicators">
                    <li data-target="#preview" data-slide-to="0" className="active"></li>
                    <li data-target="#preview" data-slide-to="1"></li>
                    <li data-target="#preview" data-slide-to="2"></li>
                </ol>
                <div className="carousel-inner">
                    <div className="carousel-item active">
                        <img className="d-block w-100" src={`${process.env.PUBLIC_URL}/images/1887.jpg`} alt="First slide"/>
                    </div>
                    <div className="carousel-item">
                        <img className="d-block w-100" src={`${process.env.PUBLIC_URL}/images/1890.jpg`} alt="Second slide"/>
                    </div>
                    <div className="carousel-item">
                        <img className="d-block w-100" src={`${process.env.PUBLIC_URL}/images/display.jpg`} alt="Third slide"/>
                    </div>
                </div>
                <a className="carousel-control-prev" href="#preview" role="button" data-slide="prev">
                    <span className="fa fa-angle-left" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                </a>
                <a className="carousel-control-next" href="#preview" role="button" data-slide="next">
                    <span className="fa fa-angle-right" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                </a>
            </div>
        )
    }
}

export default Preview